import React, { forwardRef } from "react";

import { Root, Trigger } from "@radix-ui/react-popover";

import {
	StyledPopoverContent, StyledPopoverPortal,
} from "./Popover.styles";
import { PopoverMenuProps } from "./Popover.types";


const Popover = forwardRef<HTMLDivElement, PopoverMenuProps>(({
	trigger,
	side = "bottom",
	align = "start",
	dataQA,
	ariaLabel,
	$menuWidth,
	$menuHeight,
	children,
	className,
	m,
	mb,
	mt,
	ml,
	mr,
	radixPopoverProps,
	radixPopoverContentProps,
	triggerOnClick,
	triggerOnFocus,
	$zIndex,
	isTriggerDisabled,
}, ref) => (
	<Root
		{...radixPopoverProps}
	>
		<Trigger
			asChild
			disabled={isTriggerDisabled}
			onClick={triggerOnClick}
			onFocus={triggerOnFocus}
		>
			{trigger}
		</Trigger>
		<StyledPopoverPortal
			$menuHeight={$menuHeight}
			$menuWidth={$menuWidth}
		>
			<StyledPopoverContent
				ref={ref}
				data-qa={dataQA}
				aria-label={ariaLabel}
				$menuHeight={$menuHeight}
				$menuWidth={$menuWidth}
				align={align}
				side={side}
				m={m}
				mb={mb}
				mt={mt}
				ml={ml}
				mr={mr}
				className={className}
				$zIndex={$zIndex}
				{...radixPopoverContentProps}
			>
				{children}
			</StyledPopoverContent>
		</StyledPopoverPortal>
	</Root>
),
);

Popover.displayName = "Popover";

export default Popover;
